
.donut-ring {
  position: relative;
  width: 80px;
  height: 80px;
}

.ring {
  position: absolute;
  width: 50%;
  height: 50%;
}

.ring.gray {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fafafa;
  width: 100%;
  height: 100%;
  animation: effect 1s linear infinite;
  border-radius: 100% 100% 100% 100%;
  border: 12px solid rgba(255, 255, 255, 0.3);
  border-top-color: gray;
  
}


pie {
  width: 80px;
  height: 80px;
  
  border-radius: 50%;
  background-color: #fafafa;
  
  
  
}

.ten {
  background-image:
      linear-gradient(0deg, transparent 50%, gray 50%),
      linear-gradient(10deg, transparent 50%, gray 50%);
}
.progress-center{
  width: 50%;
    height: 50%;
    background-color: #fafafa;
    position: absolute;
    top: 25%;
    left: 25%;
    border-radius: 50%;
}
.progress{
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #2ECC83;
  animation: effect 0.8s linear infinite;
  background-image:
    linear-gradient(0deg, transparent 50%, #fafafa 50%),
    linear-gradient(0deg, transparent 50%, #fafafa 50%);
      
}

@keyframes effect {
  0% {
      transform: rotate(0deg);
  }

  100% {
      transform: rotate(360deg);
  }

}
.progress2{
  position: relative;
  width: 51px;
  height: 51px;
  border-radius: 50%;
  background-color: transparent;
  animation: effect2 1.5s ease-in-out infinite;
  background-image:
      linear-gradient(0deg, transparent 50%, #fafafa 50%),
      linear-gradient(0deg, transparent 50%, #fafafa 50%);
}


@keyframes effect2 {
  0% {
    transform: rotate(0deg);
}

100% {
    transform: rotate(-360deg);
}

}

